<template>
  <div :style="{width:width,height:height}"></div>
</template>

<script>
import echarts from 'echarts'
import { debounce } from '@/utils'

export default {
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '350px'
    },
    autoResize: {
      type: Boolean,
      dafault: true
    },
    chartData: {
      type: Object
    },
    chartxaxis: {
      type: Array
    }
  },
  data () {
    return {
      chart: null
    }
  },
  created () {
    // console.log(this.chartData)
    // console.log(this.chartxaxis)
  },
  mounted () {
    this.initChart()
    if (this.autoResize) {
      this.__resizeHanlder = debounce(() => {
        if (this.chart) {
          this.chart.resize()
        }
      }, 100)
      window.addEventListener('resize', this.__resizeHanlder)
    }
  },
  watch: {
    chartData: {
      deep: true,
      handler (val) {
        this.setOptions(val, this.chartxaxis)
      }
    }
  },
  methods: {
    setOptions ({expectedData, actualData} = {}, areaChartxAxis) {
      this.chart.setOption({
        color: [' #91c7ae'],
        title: {
          text: '存储'
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          // data: ['邮件营销', '联盟广告', '视频广告', '直接访问', '搜索引擎']
          data: ['模型存储']
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            // data: ['周一', '周二', '周三', '周四', '周五', '周六', '周日']
            data: areaChartxAxis,
            axisTick: {
              show: false
            }
          }
        ],
        yAxis: [
          {
            type: 'value',
            axisLabel: {
              formatter: '{value} GB'
            },
            axisTick: {
              show: false
            },
            splitLine: {
              show: false
            }
          }
        ],
        series: [
          // {
          //   name: '邮件营销',
          //   type: 'line',
          //   stack: '总量',
          //   areaStyle: {normal: {}},
          //   data: [120, 132, 101, 134, 90, 230, 210]
          // },
          // {
          //   name: '联盟广告',
          //   type: 'line',
          //   stack: '总量',
          //   areaStyle: {normal: {}},
          //   data: [220, 182, 191, 234, 290, 330, 390]
          // },
          // {
          //   name: '视频广告',
          //   type: 'line',
          //   stack: '总量',
          //   areaStyle: {normal: {}},
          //   data: [150, 232, 201, 154, 190, 330, 410]
          // },
          // {
          //   name: '直接访问',
          //   type: 'line',
          //   stack: '总量',
          //   areaStyle: {normal: {}},
          //   data: [320, 332, 301, 334, 390, 330, 320]
          // },
          {
            name: '模型存储',
            type: 'line',
            stack: '总量',
            // label: {
            //   normal: {
            //     show: true,
            //     position: 'top'
            //   }
            // },
            areaStyle: {normal: {}},
            // data: [820, 932, 901, 934, 1290, 1330, 1320]
            data: actualData
          }
        ]
      })
    },
    initChart () {
      this.chart = echarts.init(this.$el)
      this.setOptions(this.chartData, this.chartxaxis)
    }
    // setOptions (option) {
    //   this.chart.setOption({
    //     xAxis: {
    //       type: 'category',
    //       boundaryGap: false,
    //       data: option
    //     },
    //     yAxis: {
    //       type: 'value'
    //     },
    //     series: [{
    //       data: [820, 932, 901, 934, 1290, 1330, 1320],
    //       type: 'line',
    //       areaStyle: {}
    //     }]
    //   })
    // }
  }
}
</script>



